import moment from "moment";
import "moment/locale/ko";
import { strcut } from "@/lib/utils2";
import { useRouter } from "next/router";
import Image from "next/image";

const RelatedPostCard = ({ post }) => {
  const { id, title, desc, wdate, thumbData, tag } = post || {};
  const { url, extension, alt, width, height } = thumbData || {};

  const router = useRouter();
  const handleClick = () => {
    router.push(`/${id}`);
  };

  return (
    <div
      onClick={handleClick}
      className="border border-black rounded-lg overflow-hidden cursor-pointer flex lg:block h-[100px] lg:h-full"
    >
      <div className="flex-center bg-indigo-500 w-[150px] lg:w-full lg:h-[220px] overflow-hidden shrink-0">
        {thumbData ? (
          <Image
            src={`${url}.${extension}`}
            alt={alt}
            quality={80}
            width={width}
            height={height}
          />
        ) : (
          <span className="text-white font-bold text-xl lg:text-4xl">
            #{tag}
          </span>
        )}
      </div>
      <div className="flex flex-col space-y-2 px-4 py-3 ">
        <div className="font-bold text-base lg:text-lg">
          {strcut(title, 90)}
        </div>
        <div className="text-stone-500 text-sm flex-grow hidden lg:block">
          {strcut(desc, 50)}
        </div>
        <div className="text-stone-500 text-xs border-t pt-2 hidden lg:block">
          {moment(wdate).format("YYYY년 MM월 DD일")}
        </div>
      </div>
    </div>
  );
};

const RelatedPosts = ({ postId, posts }) => {
  return (
    <div className="border-t border-black py-8 lg:py-16">
      <div className="text-center font-bold text-xl lg:text-3xl mb-8 lg:mb-16">
        Related Posts
      </div>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-y-4 lg:gap-y-8 md:gap-8">
        {posts &&
          posts
            .filter((post) => post.id !== postId)
            .map((post) => (
              <RelatedPostCard post={post} key={`relatedPost-${post.id}`} />
            ))}
      </div>
    </div>
  );
};
export default RelatedPosts;
