const baseURL = "http://127.0.0.1:8000/api";

export const getPosts = async () => {
  const res = await fetch(`${baseURL}/blog/posts`);
  const data = await res.json();
  return data;
};

export const getPostTags = async () => {
  const res = await fetch(`${baseURL}/blog/postTags`);
  const data = await res.json();
  return data;
};

export const getPostsByTag = async (tag) => {
  const res = await fetch(`${baseURL}/blog/posts/tag/${tag}`);
  const data = await res.json();
  return data;
};

export const getPostById = async (postId) => {
  const res = await fetch(`${baseURL}/blog/post/${postId}`);
  const data = await res.json();
  return data;
};

export const getPageBySlug = async (slug) => {
  const res = await fetch(`${baseURL}/blog/page/${slug}`);
  const data = await res.json();
  return data;
};
