import { useQuery } from "react-query";
import BlogPostById from "@/containers/blog/BlogPostById";
import { getPosts, getPostById } from "@/services/blog";

export default function Page(props) {
  const { data } = useQuery(["post", props.id], () => getPostById(props.id), {
    initialData: props.data,
  });
  return (
    <BlogPostById
      post={data?.post}
      blocks={data?.blocks}
      prev={data?.prev}
      next={data?.next}
      relatedPosts={data?.relatedPosts}
    />
  );
}

export const getStaticPaths = async () => {
  const posts = await getPosts();
  const paths = posts.map((post) => {
    return { params: { id: post.id.toString() } };
  });
  console.log("[id] getStaticPaths paths", JSON.stringify(paths));
  return {
    paths,
    fallback: false,
  };
};

export const getStaticProps = async ({ params }) => {
  const { id } = params;
  const data = await getPostById(id);
  return { props: { id, data } };
};
